import { PerfilUsuario } from './../../../model/objetc/perfil-usuario';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Usuario } from '../../../model/objetc/usuario';
import { Base } from '../../../model/base';
import { UsuarioService } from '../../../service/sigobra/usuario.service';
import { PermissoesAcessoService } from '../../../service/sigobra/permissoes-acesso.service';

@Component({
  selector: 'app-permissao-usuario',
  templateUrl: './permissao-usuario.page.html',
  styleUrls: ['./permissao-usuario.page.scss'],
})
export class PermissaoUsuarioPage implements OnInit {

  usuario: Usuario = new Usuario();
  perfisUsuarios: Array<PerfilUsuario>;
  usuarios: Array<Usuario>;
  constructor(private router: Router,
              private base: Base,
              private us: UsuarioService,
              private pa: PermissoesAcessoService
  ) {
    this.pesquisarPerfilUsuario();
    this.pesquisaUsuario();
  }

  ngOnInit() {
    if (this.us.flag === "01"){
      this.usuario = this.us.usuario;
      this.us.flag = "00";
    }
  }

  pesquisaUsuario() {
    this.base.present();
    return this.us.pesquisar(' ').subscribe(
      data => {
        this.base.dismiss();
        this.usuarios = data as Array<Usuario>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  usuario ' + this.base.tratarErro(error));
      }
    );
  }

  pesquisarPerfilUsuario() {
    this.pa.pesquisarPerfilUsuario(' ').subscribe(
      data => {
        this.perfisUsuarios = data as Array<PerfilUsuario>;
      },
      error => {
        this.base.mensagemErro('Falha ao consultar perfil usuário ' + this.base.tratarErro(error));
      }
    );
  }

  salvar() {
    this.base.present();
    this.us.salvar(this.usuario).subscribe(
      data => {
        this.base.dismiss();
        this.router.navigate(['perfil-usuario']);
      },
      error => {
        this.base.dismiss();
      }
    );
  }

}
